const state = {
    statusLoading: true,
};

const getters = {
    getStatusLoading: (state) => state.statusLoading
};

const actions = {
    setStatusLoading({ commit }, payload){
        setTimeout(() => {
            commit('SET_STATUS_LOADING', payload)
        }, 3000);
    },
};

const mutations = {
    SET_STATUS_LOADING: (state, data) => state.statusLoading = data.status,
};

export default {
    state,
    getters,
    actions,
    mutations
}